import React from "react";
import {
  Show,
  SimpleShowLayout,
  TextField,
  DateField,
  RichTextField,
} from "react-admin";

const PostShow = () => {
  return (
    <Show title="Post view">
      <SimpleShowLayout>
        <TextField source="title" />
        <TextField source="teaser" />
        <RichTextField source="body" />
        <DateField label="Publication date" source="published_at" />
      </SimpleShowLayout>
    </Show>
  );
};

export default PostShow;
